import { seo, pages } from "projectSettings"
import React from "react"
import { Helmet } from "react-helmet"
// import SchemaOrg from "./schema-org"
import openGraphImageUrl from "projectStatic/open-graph/default.jpg"

function Seo(props) {
  const pageData = props.pageData
  const {
    title,
    seoTitle,
    description,
    canonicalUrl,
    body,
    excerpt,
    frontmatter,
    headings,
    id,
    slug,
    tableOfContents,
    words,
    sentences,
    paragraphs,
  } = pageData

  const ogType = props.ogType || "website"

  return (
    <>
      <Helmet>
        {/* General tags */}
        <html lang="en" />
        <title>{seoTitle}</title>
        <meta name="description" content={description} />
        <meta name="robots" content="index,follow" />
        <meta name="googlebot" content="index,follow" />
        <link rel="canonical" href={canonicalUrl} />
        {/* OPENGRAPH TAGS */}
        {/* -> basic metadata */}
        <meta property="og:title" content={seoTitle} />
        <meta property="og:type" content={ogType} />
        <meta property="og:image" content={openGraphImageUrl} />
        <meta property="og:url" content={canonicalUrl} />
        {/* -> optional metadata */}
        {/* og:audio - A URL to an audio file to accompany this object. */}
        {/* og:determiner - The word that appears before this object's title in a sentence. An enum of (a, an, the, "", auto). If auto is chosen, the consumer of your data should chose between "a" or "an". Default is "" (blank). */}
        {/* og:locale - The locale these tags are marked up in. Of the format language_TERRITORY. Default is en_US. */}
        {/* og:locale:alternate - An array of other locales this page is available in. */}
        {/* og:site_name - If your object is part of a larger web site, the name which should be displayed for the overall site. e.g., "IMDb". */}
        {/* og:video - A URL to a video file that complements this object. */}
        <meta property="og:description" content={description} />
        {/* -> image metadata */}
        <meta property="og:image:alt" content={seo.siteSeoPrefix} />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />
        {/* TWITTER CARD TAGS */}
        <meta name="twitter:title" content={seoTitle} />
        <meta name="twitter:description" content={description} />
        <meta name="twitter:card" content="summary_large_image" />
        {/* <meta name="twitter:creator" content={seo.social.twitter} /> */}
        <meta name="twitter:image" content={openGraphImageUrl} />
      </Helmet>
      {/* <SchemaOrg
        //  General tags
        title={title}
      /> */}
    </>
  )
}

export default Seo

