import { seo, pages } from "projectSettings"

function getPageData(props) {
  const mdx = props.data?.mdx ? props.data.mdx : null
  const location = props?.location ? props.location : null
  //
  const body = mdx?.body
  const excerpt = mdx?.excerpt
  const frontmatter = mdx?.frontmatter
  const headings = mdx?.headings
  const id = mdx?.id
  const slug = mdx?.slug
  const tableOfContents = mdx?.tableOfContents
  const words = mdx?.words
  const sentences = mdx?.sentences
  const paragraphs = mdx?.paragraphs
  // seo
  const path = props?.path
  const pageType = getPageType()
  const pageName = getPageName()
  const title = getPageTitle().pageTitle
  let seoTitle = getPageTitle().pageSeoTitle
  let description = getPageDescription()
  const canonicalUrl = getCanonicalUrl()

  const pageData = {
    // mdx
    body,
    excerpt,
    frontmatter,
    headings,
    id,
    slug,
    tableOfContents,
    words,
    sentences,
    paragraphs,
    // seo
    title,
    seoTitle,
    description,
    canonicalUrl,
    // else
    path,
  }

  function getPageName() {
    let pageName = location.pathname.replace(/\//gi, "")

    if (pageName === "") pageName = "home"

    return pageName
  }

  function getPageType() {
    if (mdx?.frontmatter === undefined) {
      return "static"
    } else {
      return "dynamic"
    }
  }

  function getPageTitle() {
    let pageTitle, pageSeoTitle

    for (const key in pages) {
      if (pageName === key && pageType === "static") {
        pageTitle = pages[pageName].title
        pageSeoTitle = `${pages[pageName].title} - ${seo.siteSeoPrefix}`
        if (pageName === "home") pageSeoTitle = `${seo.siteSeoPrefix}`
      }
    }
    if (pageType === "dynamic") {
      pageTitle = mdx.frontmatter.title
      pageSeoTitle = `${mdx.frontmatter.title} - ${seo.siteSeoPrefix}`
    }

    if (pageTitle === undefined) {
      pageTitle = pages["notFound"].title
      pageSeoTitle = `${pages["notFound"].title} - ${seo.siteSeoPrefix}`
    }

    const pageTitles = {
      pageTitle,
      pageSeoTitle,
    }
    return pageTitles
  }

  function getPageDescription() {
    let pageDescription

    for (const key in pages) {
      if (pageName === key && pageType === "static") {
        pageDescription = pages[pageName].description
      }
    }
    if (pageType === "dynamic") {
      pageDescription = mdx.frontmatter.description
    }

    if (pageDescription === undefined) {
      pageDescription = pages[
        "notFound"
      ].description = `${pages["notFound"].description} - ${seo.siteSeoPrefix}`
    }

    if (excerpt) {
      pageDescription = excerpt
    }

    return pageDescription
  }

  function getCanonicalUrl() {
    let canonicalUrl = ""

    switch (path) {
      case "/":
        canonicalUrl = `${seo.siteUrl}/`
        break

      default:
        canonicalUrl = `${seo.siteUrl}${path}`
        break
    }

    return canonicalUrl
  }

  return pageData
}

export default getPageData
